@import "../../../scss/index.scss";

.slider-frame {
  position: relative;
  margin: 0 auto;
  overflow: hidden;
  width: 100%;

  .btn {
    position: absolute;
    top: 35%;
    vertical-align: middle !important;
    z-index: 4;
    border: 0px !important;

    &:hover {
      cursor: pointer;
      box-shadow: 0px 0px 2px 2px $action;
      -webkit-box-shadow: 0px 0px 2px 2px $action;
      transform: rotate(360deg);
      color: $action;
      border: 0px !important;
    }

    &.prev {
      left: 0px;
    }

    &.next {
      right: 0px;
    }
  }

  .story-users {
    display: flex;
    flex-direction: row;
    margin-top: 0px !important;
    margin-bottom: 10px !important;
    width: 100%;
    max-height: 160px;

    .time-line-story {
      position: relative;
      display: flex;
      margin: 0 !important;
      padding-top: 10px !important;
      padding-bottom: 10px !important;

      &__add {
        padding: 0px !important;
        margin-left: 10px !important;
        border: 1px solid $border-grey;
        border-radius: 10px;
        width: 90px;
        max-height: 140px;
        touch-action: pan-y;

        .img-story-add {
          padding: 0px !important;
          border-top-left-radius: 10px;
          border-top-right-radius: 10px;
          width: 100%;
          height: 65%;
        }

        i {
          display: flex;
          margin-left: 35px !important;
        }

        &:hover {
          cursor: pointer;
          box-shadow: 0px 0px 2px 2px $action;
          -webkit-box-shadow: 0px 0px 2px 2px $action;
          transform: rotate(360deg);
          color: $action;
        }

        span {
          display: inline-block;
          width: 100%;
          text-align: center !important;
          font-size: 10px;
          font-weight: bold;
        }
      }

      &__view {
        padding: 0px !important;
        margin-left: 10px !important;
        border: 1px solid $border-grey;
        border-radius: 10px;
        width: 90px;
        max-height: 140px;
        touch-action: pan-y;

        .img-story {
          position: relative;
          padding: 0px !important;
          border-radius: 10px;
          height: 100%;
        }

        .img-avatar {
          position: absolute;
          margin-top: 5px;
          margin-left: 5px;
          border: 2px solid $action;
          z-index: 4;

          &:hover {
            transform: scale(2);
            -moz-transform: scale(2); /* Firefox */
            -webkit-transform: scale(2); /* Chrome - Safari */
            -o-transform: scale(2);
          }
        }

        &:hover {
          cursor: pointer;
          box-shadow: 0px 0px 2px 2px $action;
          -webkit-box-shadow: 0px 0px 2px 2px $action;
          transform: rotate(360deg);
          color: $action;
        }
      }
    }
  }
}
