@import "../../scss/index.scss";

.header {
  position: relative;
  border-bottom: 1px solid #dbdbdb;
  padding: 14px 0px;
  background-color: $background-light;
  z-index: 4;

  &__logo {
    display: flex !important;
    align-items: center;

    img {
      height: 40px;
    }

    &:hover {
      transform: scale(1.5);
      -moz-transform: scale(1.5); /* Firefox */
      -webkit-transform: scale(1.5); /* Chrome - Safari */
      -o-transform: scale(1.5);
    }
  }
}
