@import "../../../scss/index.scss";

.preview-storys {
  margin-top: 15px !important;
}

.preview-story {
  height: 250px;
  border-radius: 10px;
  border: 1px solid $border-grey;
  margin: 5px !important;

  &:hover {
    cursor: pointer;
    box-shadow: 0px 0px 2px 2px $action;
    -webkit-box-shadow: 0px 0px 2px 2px $action;
    opacity: 0.9;
  }

  .img-avatar {
    position: absolute !important;
    margin-top: 5px;
    margin-left: 5px;
    border: 2px solid $action;
    z-index: 2;

    &:hover {
      transform: scale(2);
      -moz-transform: scale(2); /* Firefox */
      -webkit-transform: scale(2); /* Chrome - Safari */
      -o-transform: scale(2);
    }
  }

  .img-story {
    position: relative;
    padding: 0px !important;
    border-radius: 10px;
    height: 248px;
    width: 100%;
  }
}
